interface State {
  publicPages: PublicReducer;
}

const JobDetails: FC<any, State> = ({
  job,
  getJobDetails,
  cleaners,
  getAllCleaners,
  editJob,
}) => {
  const [formValue, setFormValue] = useState({} as any);
  const [cleanerData, setCleanerData] = useState([] as any);
  const [signedCleaners, setSignedCleaners] = useState([]);
  const [errors, setErrors] = useState("");
  const id = window.location.pathname.split("/").pop();

  useEffect(() => {
    getJobDetails(id);

    getAllCleaners({
      limit: 10,
      offset: 0,
    });
  }, [getJobDetails, getAllCleaners, id]);

  useEffect(() => {
    if (job) {
      const details = {
        ...job?.customer[0],
        ...job,
      };

      setFormValue({
        name: details.name || "",
        phone: details.phone || "",
        email: details.email || "",
        address: details.address?.replace("\n", "") || "",
        preferDate: new Date(details.preferDate?.split(" ")[0]) || "",
        cleaningTool: details.cleaningTool || "",
        basicTool: details?.cleaningTool?.basic || "",
        vacuum: details?.cleaningTool?.vacuum || "",
        cleaningToolFee: JSON.parse(details.cleaningToolFee) || "",
        durationTime: details.durationTime || "",
        pricePerHour: details.pricePerHour || "",
        total: details.total || "",
        note: details?.note || "",
        numberOfCleaners: details?.numberOfCleaners || 1,
      });

      setSignedCleaners(details.cleanerId || []);
    }

    if (cleaners) {
      setCleanerData(
        cleaners.map((item) => ({
          label: `${item.name} - ${item.phone}`,
          value: item._id,
        }))
      );
    }
  }, [job, cleaners]);

  const handleSubmit = async () => {
    if (formValue.numberOfCleaners !== signedCleaners.length) {
      setErrors(`Vui lng chn ${formValue.numberOfCleaners} ngi dn`);

      return;
    }

    const uploadData = {
      phone: formValue.phone,
      name: formValue.name,
      address: formValue.address,
      email: formValue.email,
      preferDate: formValue.preferDate,
      // time: time,
      durationTime: formValue.durationTime,
      cleaningTool: job.cleaningTool,
      cleaningToolFee: job.cleaningToolFee,
      unit: "vnd",
      note: formValue.note || "",
      numberOfCleaners: formValue.numberOfCleaners,
      pricePerHour: formValue.pricePerHour,
      cleanerId: signedCleaners,
    };

    const res = await editJob(id, uploadData);
    if (!res.error) {
      setErrors("Assigned successfully");
    }
  };

  return (
    <>
      {" "}
      <Container>
        <Title>t dch v theo gi</Title>
        <Row style={{ alignItems: "baseline" }}>
          <Col>
            <Form
              // ref={formRef as any}
              // model={model}
              onChange={() => {}}
              formValue={formValue}
            >
              <Field
                label="Tn khch hng"
                name="name"
                placeholder="Vui lng in tn ngi t dch v"
              />
              <Field
                label="S in thoi *"
                name="phone"
                placeholder="Nhp s in thoi"
              />
              <Field
                label="Email *"
                name="email"
                placeholder="Email  xc nhn n dn v sinh"
              />
              <Field
                label="a ch"
                name="address"
                // onChange={(e: any) => onChangeDistricts(e)}
              />
              <Field
                label="Ngy lm*"
                accepter={DatePicker}
                name="preferDate"
                placeholder="Chn ngy lm"
                format="dd-MM-yyyy"
              />
              <Field
                label="Gi lm"
                name="durationTime"
                // onChange={(e: any) => onChangeDistricts(e)}
              />
              <Field
                label="S tin 1 gi"
                name="pricePerHour"
                // onChange={(e: any) => onChangeDistricts(e)}
              />

              <Form.Group controlId={`tool-3`}>
                <Form.Control
                  name="tool"
                  accepter={CheckboxGroup}
                  // onChange={(e: any) => onChange(e, "tool")}
                >
                  <Checkbox
                    checked={formValue.basicTool}
                  >{`Dng c c bn - ph ph: ${formValue?.cleaningToolFee?.basic} vnd`}</Checkbox>
                  <Form.HelpText style={{ margin: "-10px 0 10px 10px" }}>
                    Chi, cy lau nh, x chu, d, vim, nc lau sn, dng c
                    chi toilet,...
                  </Form.HelpText>
                  <Checkbox
                    checked={formValue.vacuum}
                  >{`My ht bi va(ht thm hoc gh sofa, nm) - ${formValue?.cleaningToolFee?.vacuum} vnd`}</Checkbox>
                </Form.Control>
              </Form.Group>
              <Field
                label="Dn d thm"
                rows={5}
                name="note"
                accepter={Textarea}
                placeholder="Bn c th ghi ch thm nhng vic bn mun nhn vin dn v sinh ch  khi lm"
              />
              <Row>
                <div>
                  <span>Tng</span>:{" "}
                  <span style={{ color: "#01527C", fontSize: "30px" }}>
                    {formValue.total} ng
                  </span>
                </div>

                <Button
                  type="submit"
                  style={{
                    borderColor: "#042C41",
                    backgroundColor: "#042C41",
                    width: "150px",
                    height: "60px",
                    color: "#fff",
                  }}
                  onClick={handleSubmit}
                >
                  Xc Nhn
                </Button>
              </Row>
            </Form>
          </Col>
          <Col style={{ display: "flex", alignItems: "center" }}>
            {/* <span>Chn {formValue.numberOfCleaners || 1} ngi dn:</span> */}
            <CheckPicker
              placeholder={`Chn ${formValue.numberOfCleaners || 1} ngi dn:`}
              data={cleanerData}
              style={{ width: 224, marginTop: "30px" }}
              value={signedCleaners}
              onChange={setSignedCleaners}
            />
          </Col>
        </Row>
        <Modal size="sm" open={!!errors} onClose={() => setErrors("")}>
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              textAlign: "center",
              flexDirection: "column",
            }}
          >
            <Modal.Title style={{ height: "100px" }}>{errors}</Modal.Title>
            <Modal.Footer style={{ width: "100%", textAlign: "center" }}>
              <Button
                onClick={() => setErrors("")}
                appearance="primary"
                style={{ width: "150px" }}
              >
                Ok
              </Button>
            </Modal.Footer>
          </div>
        </Modal>
      </Container>
    </>
  );
};

const mapDispatchToProps = {
  getJobDetails,
  getAllCleaners,
  editJob,
};

const mapStateToProps = (state) => {
  const job = state.adminInfo.jobDetail;

  const cleaners = state.adminInfo?.cleaners?.list || [];

  return {
    job,
    cleaners,
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(JobDetails as any);
